body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.navbar {
  width: 100%;
  padding-bottom: 10px;
  padding-top: 10px;
  background-color: rgb(240, 234, 234);
}

.nav-links {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  list-style-type: none;
}

.nav-links li:last-child {
  margin-left: auto;
  margin-right: 2%;
  font-weight: bold;
  font-size: 25px;
}

.nav-links li {
  margin-right: 10%;
}

.main-content {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}

a {
  display: inline-block;
  color: black;
  padding: 5px 10px;
  border-radius: 5px;
  border-color: darkgray;
  border-width: 1px;
  border-style: solid;
  text-decoration: none;
  transition: background-color 0.3s ease;
  margin-top: 4px;
}

a:hover {
  background-color: #4f0e2d;
  color: white;
}

.contact {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-bottom: 5px;
  background-color: rgb(240, 234, 234);
}

.contact p {
  text-align: center;
  margin-top: 0px;
  margin-bottom: 0px;
  margin-right: 10px;
}

.contact a {
  font-size: 0.8rem;
  margin-left: auto;
  margin-right: 20px;
}

.contact-name {
  font-weight: bold;
}
