.data-skills-wrapper {
    display: flex;
    flex-wrap: wrap;    
}

.data-skill-set {
    flex: 0 0 300px;
    box-sizing: border-box;
    padding: 0 15px;
    padding-bottom: 20px;
    
}

h1, h2, h3, h4 {
    color: #1a202c;
}

h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    margin-top: 0px;
    padding-top: 0px;
}

h2 {
    font-size: 1.7rem;
    border-bottom: 2px solid #4a5568;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

h3 {
    font-size: 1.5rem;
    margin-bottom: 0px;
}

h4 {
    font-size: 1.1rem;
    margin-bottom: 0px;
    margin-top: 5px;
}

p, li {
    font-size: 1rem;
    line-height: 1.6;
}

li {
    padding-bottom: 3px;
}

ul {
    padding-left: 20px;
}

