.intro-section, .skills-overview, .featured-projects, .education-section {
    margin-bottom: 40px;
}

.intro-container, .skills-container, .projects-container , .education-details {
    margin-left: 15px;
}

.profile-image {
    width: 200px;
    height: auto;
    padding-left: 20px;
}

.intro-container {
    width: 400px;
}

.intro-section {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}

h1, h2, h3, h4 {
    color: #1a202c;
}

h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    margin-top: 0px;
    padding-top: 0px;
}

h2 {
    font-size: 1.7rem;
    border-bottom: 2px solid #4a5568;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

h3 {
    font-size: 1.5rem;
    margin-bottom: 0px;
}

h4 {
    font-size: 1.2rem;
    margin-bottom: 0px;
    margin-top: 5px;
}

p, li {
    font-size: 1rem;
    line-height: 1.6;
}

ul {
    padding-left: 20px;
}

.projects-container {
    display: flex;
    flex-wrap: wrap;
}

.project {
    margin-bottom: 10px;
}

.project-details {
    margin-top: 5px;
}

.education-details p, ul, li {
    margin-top: 0px;
    margin-bottom: 0px;
}

.education-details h4 {
    font-size: 1.0rem;
}

.education-date {
    font-style: italic;
}

.university-name {
    font-weight: bolder;
    
}


